import React from 'react'
import { useDrag, useDrop } from 'react-sortly'
import { Flipped } from 'react-flip-toolkit'
import { RemoveIcon, EditIcon } from './Icons'

type Props = {
  data: any
  locale: string
  removeItem: any
  openEntry: any
}

const Item = (props: Props): JSX.Element => {
  const {
    data: { id, depth, link },
    locale,
    removeItem,
    openEntry,
  } = props

  const { id: entryID } = link && link.sys ? link.sys : null
  const { title, slug } = link && link.fields ? link.fields : null
  const [, drag] = useDrag()
  const [, drop] = useDrop()
  const ref = React.useRef(null)
  drag(drop(ref))
  return (
    <Flipped flipId={id}>
      <div
        ref={drop}
        style={{ marginLeft: depth * 20, marginBottom: '0.75rem' }}
        className="Item"
      >
        <div className="Card" ref={drag}>
          <div className="Card__handle">
            {/* <Icon color="muted" size="tiny" icon="Drag" /> */}
          </div>
          <div className="Card__content">
            <div className="Card__content-container">
              <div className="Card__column">
                {title && locale && <p className="Title">{title[locale]}</p>}
              </div>
              <div className="Card__column">
                {slug && locale && (
                  <p className="Card__description">{slug[locale]}</p>
                )}
              </div>
            </div>
            <div className="Card__content-container">
              <div className="Card__column">
                <button
                  className="Button Button--action"
                  aria-label="Edit item"
                  onClick={() => openEntry(entryID)}
                >
                  <div className="Button__inner-wrapper">
                    <div className="Button__label">
                      <EditIcon
                        className="Button__icon"
                        width="24"
                        height="24"
                        color="#536171"
                      />
                    </div>
                  </div>
                </button>
              </div>
              <div className="Card__column">
                <button
                  className="Button Button--action"
                  aria-label="Remove item"
                  onClick={() => removeItem(id)}
                >
                  <div className="Button__inner-wrapper">
                    <div className="Button__label">
                      <RemoveIcon
                        className="Button__icon"
                        width="24"
                        height="24"
                        color="#536171"
                      />
                    </div>
                  </div>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Flipped>
  )
}

export default Item
