import * as React from 'react'
import { render } from 'react-dom'
import { DndProvider } from 'react-dnd'
import HTML5Backend from 'react-dnd-html5-backend'
import TouchBackend from 'react-dnd-touch-backend'
import { ContextProvider } from 'react-sortly'
import { init, FieldExtensionSDK } from 'contentful-ui-extensions-sdk'

import './index.scss'

import { PlusIcon, LinkIcon } from './components/Icons'
import isTouchDevice from './helpers/isTouchDevice'
import Items from './components/Items'

interface Props {
  sdk: FieldExtensionSDK
}

interface State {
  link?: any
  items?: any
}

const dndBackend = isTouchDevice() ? TouchBackend : HTML5Backend

export class App extends React.Component<Props, State> {
  state = {
    items: [],
  }

  constructor(props: Props) {
    super(props)
  }

  detachExternalChangeHandler: Function | null = null

  componentDidMount() {
    const { sdk } = this.props
    const { contentTypeID } = sdk.parameters.instance as any
    const items = sdk.field.getValue() || []

    sdk.window.startAutoResizer()

    // Update state when value has changed
    sdk.field.onValueChanged((value: object[] = []) => {
      this.setState({
        items: value && value.length > 0 ? [...value] : [],
      })
    })

    if (items && items.length > 0) {
      // Get all entries of content type id

      sdk.space
        .getEntries({
          content_type: contentTypeID,
        })
        .then(response => {
          // Filter entries from items
          const entries = response.items.filter(entry => {
            return items.some((item: any) => {
              return item.link.sys.id === entry.sys.id
            })
          })

          // Check if entries are updated compared to value items and update
          const updatedItems =
            entries.map(entry => {
              return items.map((item: any) => {
                const itemFields =
                  item && item.link && item.link.fields
                    ? item.link.fields
                    : null
                const entryFields = entry && entry.fields ? entry.fields : null
                const hasUpdatedFields =
                  JSON.stringify(itemFields.title) !==
                    JSON.stringify(entryFields.title) ||
                  JSON.stringify(itemFields.slug) !==
                    JSON.stringify(entryFields.slug)
                    ? true
                    : null

                if (item.link.sys.id === entry.sys.id && hasUpdatedFields) {
                  itemFields = entryFields
                }
                return item
              })
            })[0] || []

          sdk.field.setValue(updatedItems)
        })
        .catch(console.error)
    }
  }

  componentWillUnmount() {
    if (this.detachExternalChangeHandler) {
      this.detachExternalChangeHandler()
    }
  }

  openNewEntry = () => {
    const { sdk } = this.props
    const { contentTypeID } = sdk.parameters.instance as any

    // Open new entry in sidebar editor
    sdk.navigator
      .openNewEntry(contentTypeID, { slideIn: { waitForClose: true } })
      .then(({ entity }) => {
        // Update menu item with data from entry if changes have been made
        if (entity) {
          this.addItem(entity)
        }
      })
  }

  selectEntries = () => {
    const { sdk } = this.props
    const { contentTypeID } = sdk.parameters.instance as any

    // Open dialog to select multiple entries
    sdk.dialogs
      .selectMultipleEntries({ contentTypes: [contentTypeID] })
      .then(selectedEntries => {
        if (selectedEntries && selectedEntries.length > 0) {
          // Loop through selected entries and add them to menu
          selectedEntries.map(entry => this.addItem(entry))
          sdk.notifier.success(
            `Successfully added selected link${selectedEntries.length > 1 &&
              `s`} to menu`
          )
        }
      })
  }

  openEntry = (entryId: string) => {
    const { sdk } = this.props

    // Open entry in sidebar editor
    sdk.navigator
      .openEntry(entryId, { slideIn: { waitForClose: true } })
      .then(({ entity }) => {
        // Update menu item with data from entry if changes have been made
        if (entity) {
          this.updateItem(entity)
        }
      })
  }

  handleSortChange = (newItems: any) => {
    this.props.sdk.field.setValue(newItems)
  }

  addItem = (entry: any) => {
    const { sdk } = this.props
    const { items } = this.state

    const id = Date.now()
    const depth = 0
    const link = entry

    sdk.field
      .setValue([...items, { id, depth, link }])
      .then(data => {
        console.log(data)
      })
      .catch(err => {
        console.log(err)
      })
  }

  removeItem = (id: string) => {
    const { sdk } = this.props
    const items = sdk.field.getValue() || []
    const index = items.findIndex((item: any) => item.id === id)

    if (index !== -1) {
      items.splice(index, 1)
      sdk.field.setValue(items)
      sdk.notifier.success(`Successfully removed item from menu`)
    } else {
      sdk.notifier.error(`Item could not be found or`)
    }
  }

  updateItem = (entry: any) => {
    const { sdk } = this.props
    const items = sdk.field.getValue() || []

    const entryId = entry && entry.sys && entry.sys.id ? entry.sys.id : null
    const index = items.findIndex((item: any) => item.link.sys.id === entryId)

    // Update menu item with fields from entry if they have been changed
    if (index !== -1) {
      const item = items[index]
      const itemFields =
        item && item.link && item.link.fields ? item.link.fields : null
      const entryFields = entry && entry.fields ? entry.fields : null
      const hasUpdatedFields =
        JSON.stringify(itemFields.title) !==
          JSON.stringify(entryFields.title) ||
        JSON.stringify(itemFields.slug) !== JSON.stringify(entryFields.slug)
          ? true
          : null

      if (hasUpdatedFields) {
        // Update item with updated entry fields
        items[index].link.fields = entryFields
        // Update value
        sdk.field.setValue(items)
        sdk.notifier.success(`Successfully changed menu item to updated fields`)
      }
    } else {
      sdk.notifier.error(`Item could not be updated`)
    }
  }

  render = () => {
    const { sdk } = this.props
    const { items } = this.state
    const { contentTypeID } = sdk.parameters.instance

    const locale =
      sdk && sdk.field && sdk.field.locale ? sdk.field.locale : null
    const contentType =
      sdk && sdk.space
        ? sdk.space
            .getContentType(contentTypeID)
            .then(data => {
              console.log('render -> data', data)
            })
            .catch(err => {
              console.log(err)
            })
        : null

    return (
      <DndProvider backend={dndBackend}>
        <ContextProvider>
          <div className="Section">
            {items && items.length > 0 && (
              <Items
                items={items}
                locale={locale}
                onChange={this.handleSortChange}
                removeItem={this.removeItem}
                openEntry={this.openEntry}
              />
            )}
          </div>

          <div className="Section">
            <div className="Grid">
              <div className="Row">
                <div className="Column">
                  <button
                    className="TextLink TextLink--primary TextLink--spacing"
                    onClick={() => this.openNewEntry()}
                  >
                    <PlusIcon
                      className="TextLink__icon"
                      width="24"
                      height="24"
                      color="#536171"
                    />
                    Create new Link
                  </button>
                </div>
                <div className="Column">
                  <button
                    className="TextLink TextLink--primary TextLink--spacing"
                    onClick={() => this.selectEntries()}
                  >
                    <LinkIcon
                      className="TextLink__icon"
                      width="24"
                      height="24"
                      color="#536171"
                    />
                    Link existing entries
                  </button>
                </div>
              </div>
            </div>
          </div>
        </ContextProvider>
      </DndProvider>
    )
  }
}

init(sdk => {
  render(
    <App sdk={sdk as FieldExtensionSDK} />,
    document.getElementById('root')
  )
})

