import React from 'react'
import Sortly from 'react-sortly'
import { Flipper } from 'react-flip-toolkit'
import Item from './Item'

type Props = {
  items: any
  locale: string
  onChange: any
  removeItem: any
  openEntry: any
}

const Items = ({
  items,
  locale,
  onChange,
  removeItem,
  openEntry,
}: Props): JSX.Element => {
  return (
    <Flipper flipKey={items.map(({ id }) => id).join('.')}>
      <Sortly items={items} onChange={onChange}>
        {props => (
          <Item
            {...props}
            locale={locale}
            removeItem={removeItem}
            openEntry={openEntry}
          />
        )}
      </Sortly>
    </Flipper>
  )
}

export default Items
